  import React, { useState } from 'react';
  import axios from 'axios';
  import './Login.css';

  import logoExpress from '../logoExpress.png'; //    
  import { useNavigate } from 'react-router-dom';

  const Login = ({ onLogin }) => {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [loginError, setLoginError] = useState(null); //   
    const history = useNavigate();
    const handleUsernameChange = (e) => {
      setUsername(e.target.value);
    };

    const handlePasswordChange = (e) => {
      setPassword(e.target.value);
    };


    const handleLogin = () => {

      const data = {

        username: username,
        password: password,
      };

      //    
      axios.post('/login', data)
      .then((response) => {
        console.log(response.data);
    
        if (response.data === "success") {
          //      
          onLogin();
          history('/');
        } else {
          //      
          setLoginError(' .');
        }
      })
      .catch((error) => {
        console.error(error);
        setLoginError('   .');
      });
    };

    return (
      <div className="login-container">
        
          <h2>
            <img src={logoExpress} alt="" /> {/*      */}
          </h2>
        <form className="form-container">
          <label className="label">
            {username ? (
              <input type="text" className="input" value={username} onChange={handleUsernameChange} />
            ) : (
              <input type="text" className="input" placeholder="" value={username} onChange={handleUsernameChange} />
            )}
          </label>
          <label className="label">
            {password ? (
              <input type="password" className="input" value={password} onChange={handlePasswordChange} />
            ) : (
              <input type="password" className="input" placeholder="" value={password} onChange={handlePasswordChange} />
            )}
          </label>
          <button type="button" className="button" onClick={handleLogin}>
            
          </button>
          {loginError && <p className="error">{loginError}</p>}
        </form>
      </div>
    );
  };


  export default Login;
