
import React  from 'react';
import { useNavigate } from 'react-router-dom';
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import 'bootstrap/dist/css/bootstrap.min.css';
import logoExpress from '../logoExpress.png';

function Header({ isLoggedIn, onLogout }) {

  const navigate = useNavigate(); // useNavigate   

  const handleNavigation = (path) => {
    navigate(path); //   
  };

  const handleLogout = () => {
    onLogout(); //      
    handleNavigation('/'); //   
  };
    return (
        // <Navbar fixed = "top" className="bg-body-tertiary" bg = "primary" data-bs-theme ="dark">
        // <Container>
          <Navbar bg="primary" lg = "" data-bs-theme="dark" className = "bg-body-tertiary">
        <Container>
          <Navbar.Brand onClick={() => handleNavigation('/')}> 
          <img
              alt=""
              src= {logoExpress}
              width="100"
              height="30"
              className="d-inline-block align-top"
            />{' '}
            </Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link onClick={() => handleNavigation('/train')}></Nav.Link>
            <Nav.Link onClick={() => handleNavigation('/bus')}></Nav.Link>
            <Nav.Link onClick={() => handleNavigation('/lineinfo')}></Nav.Link>
            <Nav.Link onClick={() => handleNavigation('/notice')}></Nav.Link>
          </Nav>
          {isLoggedIn ? ( 
           <Nav className = "me-2">
           <Nav.Link onClick={() => handleNavigation('/mypage')}></Nav.Link>
           <Nav.Link onClick={handleLogout}></Nav.Link>
            </Nav>
          ):(
            <Nav className = "me-2">
            <Nav.Link onClick={() => handleNavigation('/login')}></Nav.Link>
            <Nav.Link onClick={() => handleNavigation('/register')}></Nav.Link>
            </Nav>
          )}
       
        </Container>
      </Navbar>
      
      
    );
  
}

export default Header;